.concentration {
  width: 2323px;
  height: 674px;
}
.concentration .topConcentration {
  width: 2323px;
  height: 103px;
}
.concentration .topConcentration .cod {
  float: left;
  text-align: center;
  width: 384px;
  height: 103px;
}
.concentration .topConcentration .cod p {
  font-size: 44px;
  line-height: 103px;
  letter-spacing: 2px;
  color: #ffffff;
  text-shadow: 0 0 30px #f4faf5;
}
.concentration .topConcentration .qishu {
  float: right;
  width: 464px;
  height: 73px;
}
.concentration .topConcentration .qishu .one {
  float: left;
  text-align: center;
  line-height: 73px;
  font-size: 36px;
  letter-spacing: 2px;
  color: #00ffff;
  width: 227px;
  height: 73px;
}
.concentration .topConcentration .qishu .three {
  float: right;
  width: 227px;
  height: 73px;
  text-align: center;
  line-height: 73px;
  font-size: 36px;
  letter-spacing: 2px;
  color: #42b6fe;
}
.districtRow {
  width: 100%;
  height: 542px;
  margin-top: 29px;
  border-image-repeat: stretch;
  overflow: hidden;
}
.districtRow .oneBox {
  display: inline-block;
  position: relative;
  width: 18%;
  height: 460px;
  margin: 50px 0 0 40px;
}
.districtRow .oneBox p {
  letter-spacing: 2px;
  position: absolute;
  font-size: 33px;
  color: #ffffff;
}
.districtRow .oneBox span {
  position: absolute;
  left: 40%;
  top: 86%;
  font-size: 40px;
  color: #0090ff;
  letter-spacing: 2px;
}
